@charset "UTF-8";
.pipeline-metadata {
  background: var(--color-sidebar-background);
  border-bottom: var(--color-sidebar-border);
  box-shadow: var(--color-sidebar-shadow); }

.kui-theme--light {
  --color-metadata-bg: #f0f1f3;
  --color-metadata-kind-token-bg: #e6e8eb; }

.kui-theme--dark {
  --color-metadata-bg: #122531;
  --color-metadata-kind-token-bg: #2a3b46; }

.pipeline-metadata {
  position: absolute;
  top: -1px;
  /* Avoids pixel rounding gaps */
  right: 0;
  bottom: -1px;
  z-index: 2;
  width: 100%;
  max-width: 400px;
  padding: 6px 0 24px 0;
  overflow-y: auto;
  background: var(--color-metadata-bg);
  transform: translateX(100%);
  transition: transform ease 0.4s; }
  .pipeline-metadata--visible {
    transform: translateX(0); }
  @media print {
    .pipeline-metadata {
      display: none; } }

.pipeline-metadata__close-button {
  flex-grow: 0;
  flex-shrink: 0;
  margin: 0.22em -8px 0 0.8em; }
  .pipeline-metadata__close-button,
  .pipeline-metadata__close-button svg {
    width: 24px;
    height: 24px; }

.pipeline-metadata__header,
.pipeline-metadata__label,
.pipeline-metadata__row {
  margin: 1.9em 36px;
  font-size: 1.48em; }

.pipeline-metadata__header {
  display: flex;
  justify-content: space-between;
  margin-top: 1.7em; }

.pipeline-metadata__icon {
  display: inline-block;
  flex-grow: 0;
  flex-shrink: 0;
  width: 30px;
  height: 30px;
  margin: 0 0.65em 0 -0.3em;
  fill: var(--color-text); }

.pipeline-metadata__title {
  flex-grow: 1;
  margin: 0;
  font-weight: normal;
  font-size: 1.33em;
  line-height: 1.6;
  word-break: break-word; }

.pipeline-metadata__list {
  margin: 0; }

.pipeline-metadata__label {
  margin-bottom: 0.7em;
  font-weight: normal;
  opacity: 0.65;
  user-select: none; }

.pipeline-metadata__row {
  margin-top: 0.7em; }

.pipeline-metadata__value {
  display: inline;
  flex-grow: 0;
  margin-right: auto;
  line-height: 1.5;
  word-break: break-word; }

.pipeline-metadata__value--kind-type,
.pipeline-metadata__value--kind-path {
  display: block;
  overflow: hidden;
  direction: rtl;
  white-space: nowrap;
  text-align: left;
  text-overflow: ellipsis; }

.pipeline-metadata__value--kind-token {
  display: inline-block;
  padding: 0.18em 0.6em 0.2em 0.6em;
  font-weight: 600;
  background: var(--color-metadata-kind-token-bg); }
  code .pipeline-metadata__value--kind-token {
    padding: 0.4em 0.8em;
    font-weight: normal;
    font-size: 0.9em;
    font-family: Consolas, Monaco, 'Courier New', Courier, monospace; }

.pipeline-metadata__value-list-expand {
  display: block;
  margin: 0.6em 0 0 0;
  padding: 0 0 0.12em 0;
  color: var(--color-text);
  font-size: 0.96em;
  background: none;
  border: 0;
  border-bottom: 1px solid transparent;
  cursor: pointer;
  opacity: 0.45;
  appearance: none; }
  .pipeline-metadata__value-list-expand:hover {
    border-bottom-color: var(--color-text);
    opacity: 0.55; }

.pipeline-metadata__value-list--no-inline {
  margin: 0;
  padding: 0;
  list-style-type: none; }

.pipeline-metadata__value-list--no-inline li {
  position: relative;
  margin-left: 1em; }
  .pipeline-metadata__value-list--no-inline li:before {
    position: absolute;
    margin-left: -1em;
    content: '▪'; }

.pipeline-metadata__value-list--inline {
  display: flex;
  flex-wrap: wrap;
  margin: -0.3em;
  padding: 0;
  list-style: none; }
  .pipeline-metadata__value-list--inline li {
    display: inline;
    margin: 0.2em 0.3em; }

.pipeline-metadata__value-list--commas li:not(:last-child):after {
  content: ', '; }

.pipeline-metadata__toolbox-container {
  position: relative;
  display: flex; }

.pipeline-metadata__toolbox {
  display: block;
  margin: -0.5em 0 0 0;
  padding: 0;
  list-style: none; }

.pipeline-metadata__run-command-value {
  display: flex;
  flex-grow: 1;
  align-items: center; }
  .pipeline-metadata__run-command-value--no-visible {
    opacity: 0; }

.pipeline-metadata__copy-button {
  width: auto;
  height: auto;
  opacity: 0.55; }
  .pipeline-metadata__copy-button:hover {
    opacity: 1; }
  .pipeline-metadata__copy-button:active {
    transform: translateY(1px);
    opacity: 0.85; }

.pipeline-metadata__copy-message {
  position: absolute;
  top: 0;
  left: 0; }
  .pipeline-metadata__copy-message--no-visible {
    display: none; }
